/* -------- APP -------- */

.app {
  background-color: #e2e2e4;
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
}

/* -------- HEADER -------- */

.app-header {
  background: linear-gradient(
    -45deg,
    #5d5d5d 0%,
    #3f3f3f 31%,
    #1c1c1c 81%
  );
  min-height: 10vh;
  max-height: 100px;
}

/* I can use these from the create-react-app starter code right? */
.app-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 33px;
  pointer-events: none;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* -------- FOOTER -------- */

.app-footer {
  background: linear-gradient(
    -45deg,
    #5d5d5d 0%,
    #3f3f3f 29%,
    #1c1c1c 85%
  );
  min-height: 10vh;
  max-height: 100px;
}

.github-link {
  color: #e0e0e0 !important;
}

.github-link:hover {
  color: white !important;
}

/* -------- BODY -------- */

.app-body {
  background: transparent;
  margin: 0 12% 0 12%;
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  min-width: 500px;
  min-height: 350px;
}

.app-body > div {
  -webkit-flex: 1 1;
          flex: 1 1;
}

/* -------- TEXT --------- */

h1, h2, h3, h4, h5, h6 {
  color: #37393f;
}

/* -------- BUTTONS --------- */

.btn-success {
  background: #3bbc32 !important;
  border-color: #3bbc32 !important;
}

.btn-danger {
  background-color: #fc5050 !important;
  border-color: #fc5050 !important;
}

/* -------- MENU --------- */
/* Not related to Menu Modal */

.menu-box {
   position: fixed !important;
   top: calc(8vh + 40px) !important;
   width: 55vh !important;
   height: 87px !important;
   min-width: 375px !important;
}

.win-message {
   position: fixed !important;
   top: 27% !important;
   height: 35px !important;
   min-width: 200px !important;
   text-align: center;
}

/* -------- TOWER -------- */

.tower {
  color: #37393f;
  height: calc((30vh + 30vw) / 2);
  border-bottom: 2vh solid;
	border-radius: 3%;
	text-align: center;
	margin: 15% 5% 0 5%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column-reverse;
	        flex-direction: column-reverse;
	-webkit-align-items: center;
	        align-items: center;
}

.tower::after {
  background: #37393f;
  content: '';
  width: 2vh;
  height: calc((30vh + 30vw) / 2);
  position: absolute;
  z-index: 0;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

/* -------- DISKS -------- */
/* there might be a better way than customizing all disks manually */

.disk {
	height: calc((5.1vh + 5.1vw) / 2);
  max-height: 5.1vh;
	display: block;
	border-radius: 25px;
	z-index: 1;
  cursor: pointer;
}

.disk1 {
  background: linear-gradient(
    -45deg,
    #82d3e5 0%,
    #4bcfed 29%,
    #06b1d8 85%
  );
  width: 23%;
}

.disk2 {
