    -45deg,
    #84b8ed 0%,
    #4aa0f7 29%,
    #066fd8 85%
  );
  width: 34%;
}

.disk3 {
  background: linear-gradient(
    -45deg,
    #a87cea 0%,
    #8c4aef 29%,
    #5a06d8 85%
  );
  width: 45%;
}

.disk4 {
  background: linear-gradient(
    -45deg,
    #c282e5 0%,
    #b64cef 29%,
    #8e06d8 85%
  );
  width: 56%;
}

.disk5 {
  background: linear-gradient(
    -45deg,
    #ff9191 0%,
    #f22e2e 29%,
    #e21a00 85%
  );
  width: 67%;
}

.disk6 {
  background: linear-gradient(
    -45deg,
    #f4b773 0%,
    #f49f3d 29%,
    #fc7a00 85%
  );
  width: 78%;
}

.disk7 {
  background: linear-gradient(
    -45deg,
    #caf490 0%,
